


const getCountryFlag = countryCode => {
    if (!countryCode) return '';
    const imgUrl = `https://www.countryflags.io/${countryCode.toLowerCase()}/shiny/32.png`
    return imgUrl
}

const _getDataAsArray = dataObject => {
    if (Array.isArray(dataObject)) return dataObject;
    const normalizedData = Object.entries(dataObject).map(([country, data]) => {
        const countryDetails = countries.find(c => c.name.toLowerCase() === country.toLowerCase())
        const countryCode = countryDetails?.code
        const imgUrl = getCountryFlag(countryCode)
        data.forEach((day, i) => {
            if (i === 0) {
                day.deathGrowth = 1
                day.confirmedGrowth = 1
            }
            else {
                day.deathGrowth = data[i - 1].deaths ? (day.deaths / data[i - 1].deaths) : 1
                day.confirmedGrowth = data[i - 1].confirmed ? (day.confirmed / data[i - 1].confirmed) : 1
            }
        })
        return {country, data, imgUrl}
    }).filter(c => c.country !== 'Diamond Princess')

    return normalizedData
}

const fetchData = async() => {
    let dataResponse = { countriesData: [], biggestConfirmedNumber: 0};
        try {
            const respone = await fetch('https://pomber.github.io/covid19/timeseries.json')
            const data = await respone?.json()
            if (data) {
                dataResponse.countriesData = _getDataAsArray(data);
            }
    
        } catch(err) {
            console.log('Error Fetching data: ',err)
        }
    const biggestConfirmedNumber = Math.max(...dataResponse.countriesData.map(d => d.data[d.data.length - 1].confirmed))
    if (biggestConfirmedNumber) dataResponse.biggestConfirmedNumber = biggestConfirmedNumber;
    return dataResponse

}

export default {
    fetchData
}

